import React from "react";
import CardSections from "./CardSections";
import CardSectionHeader from "./cardSectionHeader";

const Cards = ({ data }) => {
  return (
    <div className="main_width">
      <div>
        <span className="text-[25px] border-b-4 rounded-[4px] border-green-700 font-bold">
          Featured Stories
        </span>
      </div>

      {data.map((section, index) => (
        <div key={section.Id || index} className="my-14">
          <CardSectionHeader title={section.CategoryName} />
          <CardSections cards={section.CategoryPostList || []} />
        </div>
      ))}
    </div>
  );
};

export default Cards;
